header {
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	z-index: 2;
	background-color: #f1faee;
	width: 100%;
}

header h1 {
	width: 100%;
	text-align: center;
	font-size: 4rem;
	color: #457b9d;
	font-family: 'PT Sans', sans-serif;
}

header h1:hover {
	cursor: pointer;
}
